.landing {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #111;
  color: #fff;
  overflow: hidden;
  background-image: url(../images/179.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  .header {
    position: absolute;
    top: -100%;
    left: 50%;
    transform: translateX(-50%);
    &__wrap {
      padding: 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 60vw;
    }
    &__left {
      display: flex;
      align-items: center;
      gap: 2rem;
      & li {
        font-size: 18px;
        font-weight: 700;
      }
    }
    @media (max-width: 1200px) {
      &__wrap {
        width: 100vw;
      }
    }
    @media (max-width: 500px) {
      .hide {
        display: none;
      }
    }
  }
  .socials {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -100%;
    display: flex;
    align-items: center;
    gap: 1rem;
  }

  .scrollDown {
    position: absolute;
    bottom: -100%;
    left: 50%;
    transform: translateX(-50%);
    &__wheel {
      height: 42px;
      width: 24px;
      border-radius: 10px;
      border: 2px solid #fff;
      &--inner {
        position: absolute;
        left: 10px;
        margin-top: 5px;
        width: 4px;
        height: 4px;
        background: #fff;
        border-radius: 50%;
        animation: movingwheel 0.5s linear infinite;
        @keyframes movingwheel {
          0% {
            top: 1px;
          }
          25% {
            top: 2px;
          }
          50% {
            top: 3px;
          }
          75% {
            top: 2px;
          }
          100% {
            top: 1px;
          }
        }
      }
    }
    .scrollDown__arrows {
      display: flex;
      flex-direction: column;
      align-items: center;
      .scrollDown__arrow {
        width: 15px;
        height: 15px;
        border-right: 2px solid #fff;
        border-bottom: 2px solid #fff;
        transform: rotate(45deg);
        animation: scrollAnim 1s ease-in-out infinite;
        animation-direction: alternate;
        &:nth-child(1) {
          animation-delay: 0.1s;
        }
        &:nth-child(2) {
          animation-delay: 0.2s;
        }
        &:nth-child(3) {
          animation-delay: 0.3s;
        }
        @keyframes scrollAnim {
          0% {
            opacity: 0;
          }
          50% {
            opacity: 0.5;
          }
          100% {
            opacity: 1;
          }
        }
      }
    }
  }
  .intro {
    position: absolute;
    top: 20%;
    left: 1rem;
    width: 100%;
    height: 60%;
    color: #fff;
    h4 {
      font-size: 33px;
      line-height: 100%;
    }
    h1 {
      font-size: 35px;
      width: 93%;
      padding-top: 1.5rem;
      line-height: 100%;
    }
    @media (min-width: 600px) {
      h4 {
        font-size: 40px;
      }
      h1 {
        font-size: 55px;
      }
    }
    @media (min-width: 1200px) {
      h4 {
        font-size: 50px;
      }
      h1 {
        font-size: 68px;
      }
    }
  }
}
