@import url("https://fonts.googleapis.com/css2?family=Montserrat&family=Orbitron&family=Poppins:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat&family=Orbitron&family=Poppins:wght@300;400&display=swap");

.skills {
  position: relative;
  font-size: 32px;
  
  background-color: #121212;
  color: #fff;
  overflow: hidden;
  font-family: "Orbitron", sans-serif;
  padding: 5px;
  margin: 8;
  text-align: center; /* Set text-align to center for the container */
}

.skills ul {
  display: inline-block;
  width: 100%; /* Full width by default */
  margin: 0;
  padding: 0;
}

.skills li {
  list-style: none;
  margin-bottom: 5px;
  text-align: left;
  padding-left: 15%; /* Adjust the padding as needed */
}

@media only screen and (min-width: 1401px) {
  .skills ul {
    width: 30%;
    margin: 0 auto;
  }

  .skills li {
    font-size: 32px;
  }
}

@media only screen and (min-width: 901px) and (max-width: 1400px) {
  .skills ul {
    width: 48%; /* Adjust the width as needed */
    margin: 0 auto; /* Center the columns */
  }

  .skills li {
    font-size: 32px;
  }
}

@media only screen and (min-width: 600px) and (max-width: 900px) {
  .skills ul {
    width: 100%; /* Adjust the width as needed */
    margin: 0 auto; /* Center the columns */
  }

  .skills li {
    padding-left: 30%;
    font-size: 32px;
  }
}

@media only screen and (min-width: 440px) and (max-width: 599px) {
  /* For screens 900px and smaller, create a full-width layout */
  .skills ul {
    width: 100%; /* Adjust the width as needed */
    margin: 0 auto; /* Center the columns */
  }
  .skills li {
    font-size: 24px;
    padding-left: 25%;
  }
}
@media only screen and (min-width: 370px) and (max-width: 439px) {
  /* For screens 900px and smaller, create a full-width layout */
  .skills ul {
    width: 100%; /* Adjust the width as needed */
    margin: 0 auto; /* Center the columns */
  }
  .skills li {
    font-size: 24px;
    padding-left: 20%;
  }
}

@media only screen and (max-width: 369px) {
  /* For screens 900px and smaller, create a full-width layout */
  .skills ul {
    width: 100%; /* Adjust the width as needed */
    margin: 0 auto; /* Center the columns */
  }
  .skills li {
    font-size: 24px;
    padding-left: 10%;
  }
}

