.projects {
  padding: 1.5rem;
  &__header {
    font-size: 60px;
    font-style: italic;
    padding-bottom: 12px;
  }
}

.project {
  &__header {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 1rem;
    font-family: sans-serif;
    span {
      border-top: 1px solid #000;
      padding: 1rem;
      font-size: 30px;
      text-align: center;
    }
  }
  &__infos {
    margin-top: 1rem;
    &--name {
      position: relative;
      font-size: 3vw;
      font-family: sans-serif;
      display: flex;
      align-items: center;
      font-weight: 400;
    }
  }
  &__img {
    position: relative;
    width: 100%;
    height: 700px;
    img {
      width: 60vw;
      height: 600px;
      object-fit: cover;
      position: absolute;
      right: 10vw;
      top: 3rem;
      border-radius: 12px;
    }
  }
  &__links {
    padding-top: 3rem;
    button {
      &:before {
        background: #000;
      }
      &:after {
        display: none;
      }
    }
  }
  &__tags {
    position: absolute;
    bottom: 1rem;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    bottom: 40%;
    left: 10%;
  }

  a{
    font-family: "Orbitron", sans-serif;
  }
}

@media (max-width: 901px) {
  .projects {
    padding: 3rem;
  }

  .project__img img {
    width: 74vw;
    right: 5rem;
  }

  .project__infos--name {
    font-size: 38px;
  }
}

@media (max-width: 900px) {
  .projects {
    padding: 1rem;
  }

  .project__tags {
    flex-direction: row;
    top: 0;
    bottom: 0;
    left: 0;
    
  }

  .project__img img {
    width: 100vw;
    right: 0;
  }
}
